import React from "react";
import Carousel from "react-bootstrap/Carousel";
import "./Hero.scss";

const images = [
  "https://i.imgur.com/PSco4yH.png", // Desarrollamos confianza
  "https://i.imgur.com/Vatzj1S.png", // +20 años de trayectoria
  "https://i.imgur.com/EmJuiEw.png", // Desarrollamos desde el momento 0
  "https://i.imgur.com/uvuMTgl.png", // Basa sello de calidad
];

const imagesMob = [
  "https://i.imgur.com/9fVvYug.png", //
  "https://i.imgur.com/mJiqDLB.png", //
  "https://i.imgur.com/16vtDEz.png", //
  "https://i.imgur.com/dMn2zAC.png", //
  "https://i.imgur.com/V20JtYN.png", //
  "https://i.imgur.com/TzEaVc2.png", //
];

const Hero = () => {
  let details = navigator.userAgent;
  let regexp = /android|iphone|kindle|ipad/i;
  let isMobileDevice = regexp.test(details);

  return (
    <div className="sec-hero">
      <Carousel className="car" id="inicio" interval={4500}>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[0] : images[0]}
            alt="First slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Desarrollamos confianza</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[1] : images[1]}
            alt="Second slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>+20 años de trayectoria</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[2] : images[2]}
            alt="Tercer slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Desarrollamos desde el momento 0</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[3] : images[3]}
            alt="Cuarto slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>BASA sello de garantía</h3>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    </div>
  );
};

export default Hero;
