import React from "react";
import { Row } from "react-bootstrap";
import Ubicacion from "../Home/Ubicacion/Ubicacion";
import Contact from "../Layouts/Contact/Contact";
import { noticias } from "../utils/noticias";
import NoticiaElement from "./NoticiaElement/NoticiaElement";
import "./Prensa.scss";

const Prensa = () => {
  return (
    <>
      <section className="sec_prensa">
        <Row className="row_portada">
          <img
            src="https://i.imgur.com/0PuEKhc.png"
            alt=""
            className="img_portada"
          />
          <h1 className="h1_prensa">Prensa</h1>
        </Row>
        {noticias.map((el, i) => (
          <NoticiaElement
            title={el.title}
            img={el.img}
            desc={el.desc}
            last={i === noticias.length - 1}
          />
        ))}
      </section>
      <Contact />
      <Ubicacion />
    </>
  );
};

export default Prensa;
