import React from "react";
import { Button } from "react-bootstrap";
import Carousel from "react-bootstrap/Carousel";
import { Link } from "react-router-dom";
import Ubicacion from "../Home/Ubicacion/Ubicacion";
import Contact from "../Layouts/Contact/Contact";

import "./Constructora.scss";

const images = [
  "https://i.imgur.com/LjksDq5.png", // +300 Obras realizadas
  "https://i.imgur.com/WLc9irJ.png", // 20 años nos avalan
  "https://i.imgur.com/jCyymsF.png", // Equipo de profesionales
  "https://i.imgur.com/twOqzrQ.png", // Proyectos sustentables
];

const imagesMob = [
  "https://i.imgur.com/9fVvYug.png", //
  "https://i.imgur.com/mJiqDLB.png", //
  "https://i.imgur.com/16vtDEz.png", //
  "https://i.imgur.com/dMn2zAC.png", //
  "https://i.imgur.com/V20JtYN.png", //
  "https://i.imgur.com/TzEaVc2.png", //
];

const Constructora = () => {
  let details = navigator.userAgent;
  let regexp = /android|iphone|kindle|ipad/i;
  let isMobileDevice = regexp.test(details);

  return (
    <section className="sec-constructora">
      <Carousel className="car" id="inicio" interval={4500}>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[0] : images[0]}
            alt="First slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>+300 obras realizadas</h3>
            <p>
              Somos una empresa dedicada al desarrollo y construcción de
              proyectos <br />
              inmobiliarios rentables, innovadores, eficientes y de calidad
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[1] : images[1]}
            alt="Second slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>20 años nos avalan</h3>
            <p>
              A lo largo de 20 años hemos realizado más de 300 obras <br /> a lo
              largo y ancho del nordeste
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[2] : images[2]}
            alt="Tercer slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Equipo de profesionales</h3>
            <Link to="/constructora/profesionales">
              <Button variant="outline-light">Conocenos</Button>
            </Link>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[3] : images[3]}
            alt="Cuarto slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Proyectos sustentables</h3>
            <p>
              Nos comprometemos con la utilización de nuevas tecnologías que
              promuevan <br /> el cuidado al medio ambiente
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    </section>
  );
};

export default Constructora;
